:root {
  --ss-bg-color: #10121e;
  --ss-border-color: #11cc49;
  --ss-primary-color: #11cc49;
  --ss-font-color: #fff;
}

body {
  color: #0fa817;
  background-color: #000;
  background-image: url("backgrCat.6963834b.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  font-family: Franklin Gothic Medium, Arial Narrow, Arial, sans-serif;
}

.cat-info {
  margin: 40px 0;
  display: grid;
  position: relative;
  left: 60%;
}

.cat-image {
  height: 400px;
  border-radius: 5px;
  box-shadow: 0 0 50px 15px #03d352;
}

.cat-name, .cat-description, .cat-temperament {
  max-width: 500px;
  text-shadow: 4px 4px 15px rgba(0, 0, 0, .6);
}

.cat-name, .cat-span, .success, .error {
  text-transform: uppercase;
  color: #fcb00d;
}

.loader-container {
  width: 100vw;
  height: 50vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.loader {
  border: thick double #30c243;
}

.success, .error {
  justify-content: center;
  display: flex;
  position: relative;
  top: -90px;
}

@-moz-keyframes spin {
  100% {
    -moz-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  display: none;
}

